.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.text{
  color: #FFF;
  margin: 0 auto;
  text-align: center;
  font-weight: 700;
  font-size: 80px;
  font-family: 'Dancing Script', cursive;
  text-shadow: -4px 3px 0 #34d16e, -14px 7px 0 #0a0e27;
}
.outer-box{
  background: -webkit-linear-gradient(90deg, #65997d 10%, #fccb6e 90%);
  background:    -moz-linear-gradient(90deg, #65997d 10%, #fccb6e 90%);
  background:     -ms-linear-gradient(90deg, #65997d 10%, #fccb6e 90%);
  background:      -o-linear-gradient(90deg, #65997d 10%, #fccb6e 90%);
  background:         linear-gradient(90deg, #65997d 10%, #fccb6e 90%);
  width: 650px;
  margin: auto;
  padding: 10px 30px;
  margin-top: 30px;
  border-radius: 20px;
  box-shadow: 0px 2px 2px rgba(0,0,0,0.6), 0px 2px 12px rgba(0,0,0,0.3);
}
.todo-createdAt{
  color:green;
}
.todo-container{
  background-color: #fff;
  width: 80%;
  margin: auto;
  padding: 10px 30px;
  margin-top: 30px;
  border-radius: 20px;
  box-shadow: 0px 2px 2px rgba(0,0,0,0.6), 0px 2px 12px rgba(0,0,0,0.3);
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
